import React from "react";
import { Route, Routes } from "react-router-dom";

const Apis = React.lazy(() => import("../api/apis"));
const Api = React.lazy(() => import("../api/api"));

export default function ApisRoute() {
  return (
    <Routes>
        <Route path="endpoints" element={<Apis />} />
        <Route path="endpoints/:id" element={<Api />} />
    </Routes>
  );
}
